<template>
  <div id="defaultwindow">
    <div id="myChart" :style="{width: '300px', height: '300px'}"></div>
  </div>

</template>

<script>
  export default {
    name: "Default",
    data() {
      return {

      }
    },
    methods: {
      drawLine(){
        // domecharts
        let myChart = this.$echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
            title: { text: 'Vueecharts' },
            tooltip: {},
            xAxis: {
                data: ["","","","","",""]
            },
            yAxis: {},
            series: [{
                name: '',
                type: 'bar',
                data: [5, 20, 36, 10, 10, 20]
            }]
        });
    }
    },
    created() {

    },
    mounted() {
      this.drawLine();
    }

  }
</script>

<style scoped>

</style>



