<template>
  <div id="doctorswindow">
    <el-table
      :data="tableData"
      stripe
      border
      height="100%"
      width="100%"
      @row-dblclick="doubleclickrow">
      <el-table-column>
        <template slot="header" slot-scope="scope">
          <div style="width: 15%;float: left">
            <el-select v-model="value" filterable clearable placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div style="width: 20%;float: left;margin-left: 10px">
            <el-input
              v-model="search"
              size="mini"
              placeholder=""
            />
          </div>
        </template>

        <el-table-column
          sortable
          prop="id"
          label="Id"
          v-if="false">
        </el-table-column>
        <el-table-column
          sortable
          prop="doctorId"
          label="doctorId"
          v-if="false">
        </el-table-column>
        <el-table-column
          sortable
          prop="patientName"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          sortable
          prop="sex"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          sortable
          prop="age"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          sortable
          prop="phoneNum"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          sortable
          prop="feature"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          sortable
          prop="diagnosis"
          label=""
          width="180">
        </el-table-column>
        <el-table-column
          sortable
          prop="treatment"
          label=""
          width="180">
        </el-table-column>
      </el-table-column>
    </el-table>
    <el-pagination
      @next-click="handleNextClick"
      @prev-click="handlePrevClick"
      @current-change="handleCurrentChange"
      :current-page="currentpage"
      background
      layout="prev, pager, next"
      :total="tableData_length"
    >
    </el-pagination>
  </div>
</template>

<script>
  export default {
    name: "AllPrescriptions",
    data() {
      return {
        allPrescriptions: '',
        tableData: [],
        search: "",
        currentpage: 1, //
        tableData_length: 0, //

        options: [{
          value: 'patientName',
          label: ''
        }, {
          value: 'sex',
          label: ''
        }, {
          value: 'age',
          label: ''
        }, {
          value: 'feature',
          label: ''
        }, {
          value: 'diagnosis',
          label: ''
        }, {
          value: 'treatment',
          label: ''
        }],
        value: ''
      }
    },
    methods: {
      doubleclickrow(row) {
        this.$router.push({name: 'prescriptiondetail', params: {prescriptionId: row.id}});
      },
      handleCurrentChange(val) {
        // 
        this.currentpage = val;
        this.handleTableData();
      },
      handlePrevClick(val) {
        // 
        this.currentpage = val;
        this.handleTableData();
      },
      handleNextClick(val) {
        // 
        //valval2
        //val currentpage, currentpage =>
        this.currentpage = val;
        //handleTableData 
        this.handleTableData();
      },
      handleTableData() {
        this.tableData_length = this.allPrescriptions.length;
        this.tableData = this.allPrescriptions.slice(
          (this.currentpage - 1) * 10,
          this.currentpage * 10
        );
      },
      handleSearch(val) {
        let search = val;
        if (search == "") {
          this.tableData = this.allPrescriptions;
          this.tableData_length = this.tableData.length;
        }
        if (search != "") {
          if (this.value == '') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.patientName.toLowerCase().includes(search.toLowerCase())
                || data.sex.toLowerCase().includes(search.toLowerCase())
                || data.age.toLowerCase().includes(search.toLowerCase())
                || data.phoneNum.toLowerCase().includes(search.toLowerCase())
                || data.feature.toLowerCase().includes(search.toLowerCase())
                || data.diagnosis.toLowerCase().includes(search.toLowerCase())
                || data.treatment.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'patientName') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.patientName.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'sex') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.sex.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'age') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.age.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'phoneNum') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.phoneNum.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'feature') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.feature.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'diagnosis') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.diagnosis.toLowerCase().includes(search.toLowerCase())
            );
          } else if (this.value == 'treatment') {
            this.tableData = this.allPrescriptions.filter(
              (data) =>
                !search || data.treatment.toLowerCase().includes(search.toLowerCase())
            );
          }

          this.tableData_length = this.tableData.length;
        }
      },
    },
    created() {
      let params = new FormData();
      params.append('keyword', this.$cookies.get('doctorId'));
      params.append('searchtype', 'doctorId');
      params.append('operatorId', this.$cookies.get('operatorId'));
      this.$axios({
        method: 'post',
        url: '/api/query_prescription/',
        data: params
      }).then(function (response) {
        this.allPrescriptions = response.data.data;
        this.tableData = this.allPrescriptions;
        this.tableData_length = this.allPrescriptions.length;
      }.bind(this));
    },
    watch: {
      //watchinput,watch search()
      search(newVal) {
        this.handleSearch(newVal);
      },
    },
  }

</script>

<style scoped>
  #doctorswindow {
    height: 100%;
  }
</style>



